body {
  margin: 0;
  font-family: 'Poppins', 'Roboto', sans-serif;
  background: linear-gradient(120deg, #e0eafc, #cfdef3);
  min-height: 100vh;
  transition: background 1s;
}

.theme-dark {
  background: linear-gradient(120deg, #232526, #414345);
  color: #f3f6f9;
  transition: background 1s;
}

.theme-light {
  background: linear-gradient(120deg, #e0eafc, #cfdef3);
  color: #232526;
  transition: background 1s;
}

.glassy-nav,
.glassy-section,
.glassy-hero,
.glassy-card,
.glassy-footer {
  background: rgba(255,255,255,0.85);
  border-radius: 18px;
  box-shadow: 0 6px 32px rgba(48,60,94,0.11);
  backdrop-filter: blur(6px);
  animation: fadeInUp 1s;
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(40px);}
  to { opacity: 1; transform: translateY(0);}
}

.nav-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem 3rem;
  position: sticky;
  top: 0;
  z-index: 10;
  opacity: 0.99;
  animation: fadeInDown 1.1s;
}

@keyframes fadeInDown {
  from { opacity: 0; transform: translateY(-40px);}
  to { opacity: 1; transform: translateY(0);}
}

.avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  margin-right: 12px;
  box-shadow: 0 4px 24px rgba(48,60,94,0.12);
  animation: popIn 1.3s cubic-bezier(.62,.28,.23,.99);
}

@keyframes popIn {
  0% { transform: scale(0);}
  80% { transform: scale(1.07);}
  100% { transform: scale(1);}
}

.brand-text {
  font-weight: bold;
  font-size: 1.5rem;
  letter-spacing: 2px;
  color: #2196f3;
  text-shadow: 0 2px 8px #21cbf366;
  animation: fadeInRight 1s;
}

@keyframes fadeInRight {
  from { opacity: 0; transform: translateX(-30px);}
  to { opacity: 1; transform: translateX(0);}
}

.nav-links a {
  margin: 0 1rem;
  text-decoration: none;
  color: inherit;
  font-weight: 500;
  letter-spacing: 1px;
  transition: color 0.2s, border-bottom 0.2s;
  border-bottom: 2px solid transparent;
  position: relative;
}
.nav-links a:hover {
  color: #21cbf3;
  border-bottom: 2px solid #21cbf3;
}

.toggle-btn {
  background: transparent;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #21cbf3;
  transition: color 0.2s, transform 0.2s;
}
.toggle-btn:hover {
  color: #2196f3;
  transform: rotate(-20deg) scale(1.2);
}

.hero-section {
  text-align: center;
  margin-top: 3rem;
  padding: 2.3rem 1rem 0.3rem 1rem;
  animation: fadeInUp 1.2s;
}

.hero-title {
  font-size: 3.3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, #2196f3 60%, #21cbf3 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: colorPulse 2s infinite linear;
}

@keyframes colorPulse {
  0%, 100% { filter: brightness(1);}
  50% { filter: brightness(1.25);}
}

.hero-role {
  font-size: 1.3rem;
  animation: fadeIn 1.5s;
  color: #2196f3;
  letter-spacing: 2px;
}

@keyframes fadeIn {
  from { opacity: 0;}
  to { opacity: 1;}
}

.pulse-card {
  display: inline-block;
  background: #e3f6fd;
  border-radius: 24px;
  padding: 0.6em 1.3em;
  margin-top: 1em;
  box-shadow: 0 2px 8px rgba(33,203,243,0.16);
  animation: fadeInLeft 1.4s;
}

@keyframes fadeInLeft {
  from { opacity: 0; transform: translateX(-30px);}
  to { opacity: 1; transform: translateX(0);}
}

.pulse-dot {
  display: inline-block;
  width: 13px;
  height: 13px;
  background: #21cbf3;
  border-radius: 50%;
  margin-right: 8px;
  animation: pulse 1.2s infinite;
}
@keyframes pulse {
  0% { box-shadow: 0 0 0 0 #21cbf3; }
  70% { box-shadow: 0 0 0 13px #21cbf333; }
  100% { box-shadow: 0 0 0 0 #21cbf3; }
}

.hero-socials a {
  margin: 0 0.7rem;
  color: #2196f3;
  font-size: 1.5rem;
  text-decoration: none;
  transition: color 0.2s, transform 0.2s;
  display: inline-block;
  animation: fadeInUp 1.7s;
}
.hero-socials a:hover {
  color: #21cbf3;
  transform: scale(1.18) rotate(-8deg);
}

.section {
  margin: 2.5rem auto;
  padding: 2.2rem 1.2rem;
  max-width: 950px;
  animation: fadeInUp 1.1s;
  position: relative;
  z-index: 1;
}

.section-header {
  display: flex;
  align-items: center;
  font-size: 2rem;
  gap: 1.2em;
  margin-bottom: 1.7rem;
  animation: floatIcon 2.5s infinite;
}

@keyframes floatIcon {
  0%, 100% { transform: translateY(0);}
  50% { transform: translateY(-8px);}
}

.section-icon {
  font-size: 2.5rem;
  animation: bounceIcon 1.7s infinite;
}

@keyframes bounceIcon {
  0%, 100% { transform: scale(1);}
  50% { transform: scale(1.13);}
}

.about-flex {
  display: flex;
  gap: 2rem;
  align-items: flex-start;
  flex-wrap: wrap;
  animation: fadeInUp 1.3s;
}
.about-image img {
  width: 170px;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(48,60,94,0.13);
  animation: popIn 1.4s cubic-bezier(.62,.28,.23,.99);
}

.skills-flex {
  display: flex;
  flex-wrap: wrap;
  gap: 1.2rem;
  margin-top: 1.2rem;
  animation: fadeInRight 1.2s;
}

.skill-pill {
  background: linear-gradient(90deg, #2196f3, #21cbf3);
  color: white;
  border-radius: 32px;
  padding: 0.9em 1.7em;
  font-weight: 600;
  box-shadow: 0 2px 12px rgba(33,203,243,0.18);
  margin-bottom: 0.5rem;
  animation: popIn 1s cubic-bezier(.62,.28,.23,.99);
  transition: transform 0.2s, box-shadow 0.2s;
}
.skill-pill:hover {
  transform: scale(1.12);
  box-shadow: 0 4px 20px #21cbf355;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, 1fr));
  gap: 2.2rem;
  animation: fadeInUp 1.5s;
}

.project-card {
  padding: 1.7rem;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(48,60,94,0.1);
  background: rgba(255,255,255,0.97);
  transition: transform 0.2s, box-shadow 0.2s;
  animation: fadeInUp 1.2s;
  position: relative;
  overflow: hidden;
}
.project-card:hover {
  transform: translateY(-8px) scale(1.04);
  box-shadow: 0 6px 32px #21cbf366;
}

.project-badge {
  background: linear-gradient(90deg, #2196f3, #21cbf3);
  color: #fff;
  border-radius: 12px;
  padding: 0.3em 1em;
  font-size: 0.9em;
  margin-left: 0.7em;
  font-weight: 600;
  box-shadow: 0 2px 8px #21cbf333;
}

.card-actions a {
  margin-right: 1.2rem;
  font-weight: 600;
  text-decoration: none;
  color: #2196f3;
  transition: color 0.2s, transform 0.2s;
}
.card-actions a:hover {
  color: #21cbf3;
  transform: scale(1.13);
}

.timeline {
  border-left: 4px solid #21cbf3;
  padding-left: 1rem;
  position: relative;
  animation: fadeInLeft 1.2s;
}
.timeline-item {
  margin-bottom: 2rem;
  position: relative;
  animation: fadeInUp 1.2s;
}
.timeline-dot {
  position: absolute;
  left: -1.25rem;
  top: 0.8rem;
  width: 14px;
  height: 14px;
  background: #21cbf3;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #e0eafc;
  animation: bounceIcon 1.3s infinite;
}

.timeline-date {
  font-size: 1em;
  color: #2196f3;
  font-weight: 500;
  letter-spacing: 1px;
}

.edu-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  animation: fadeInUp 1.5s;
}
.edu-card {
  background: rgba(33,203,243,0.10);
  border-radius: 14px;
  padding: 1.3rem;
  box-shadow: 0 2px 8px rgba(33,203,243,0.1);
  transition: transform 0.2s;
  animation: popIn 1.2s cubic-bezier(.62,.28,.23,.99);
}
.edu-card:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 20px #21cbf355;
}

.edu-details {
  font-size: 0.97em;
  color: #2196f3;
  letter-spacing: 1px;
}

.cert-list {
  columns: 2;
  margin-top: 1.2rem;
  animation: fadeInRight 1.2s;
}
.cert-list li {
  margin-bottom: 0.7rem;
  font-size: 1.03em;
  transition: color 0.2s;
  cursor: pointer;
}
.cert-list li:hover {
  color: #21cbf3;
  font-weight: 600;
}

.download-btn {
  display: inline-block;
  background: linear-gradient(90deg,#2196f3,#21cbf3);
  color: #fff;
  font-weight: 700;
  border-radius: 26px;
  padding: 0.8em 2.2em;
  text-decoration: none;
  margin-top: 1.3rem;
  box-shadow: 0 2px 8px rgba(33,203,243,0.16);
  transition: background 0.2s, transform 0.2s;
  font-size: 1.1rem;
  animation: popIn 1.3s cubic-bezier(.62,.28,.23,.99);
}
.download-btn:hover {
  background: linear-gradient(90deg,#21cbf3,#2196f3);
  transform: scale(1.07);
}

.footer {
  text-align: center;
  padding: 2.3rem 1rem;
  background: rgba(33,203,243,0.13);
  border-top-left-radius: 28px;
  border-top-right-radius: 28px;
  margin-top: 2.2rem;
  animation: fadeInUp 1.3s;
}

.footer-socials a {
  margin: 0 1.3rem;
  color: #2196f3;
  text-decoration: none;
  font-size: 1.25rem;
  transition: color 0.2s, transform 0.2s;
  animation: floatIcon 2.7s infinite;
}
.footer-socials a:hover {
  color: #21cbf3;
  transform: scale(1.13) rotate(-8deg);
}